export default {
  props: {
    current: {
      type: Number,
      default: 1
    },
    total: {
      type: Number,
      default: 0
    },
    limit: {
      type: Number,
      default: 10
    },
    visibleNumber: {
      type: Number,
      default: 10
    }
  },
  computed: {
    // 总页数
    pageNumber() {
      return Math.ceil(this.total / this.limit);
    },

    // 得到显示的最小数字
    visibleMin() {
      let min = this.current - Math.floor(this.visibleNumber / 2);

      if (min < 1) {
        min = 1;
      }

      return min;
    },

    visibleMax() {
      let max = this.visibleMin + this.visibleNumber - 1;

      if (max > this.pageNumber) {
        max = this.pageNumber;
      }

      return max;
    },

    numbers() {
      let nums = [];

      for (let i = this.visibleMin; i <= this.visibleMax; i++) {
        nums.push(i);
      }

      return nums;
    }

  },
  methods: {
    handleClick(newPage) {
      if (newPage < 1) {
        newPage = 1;
      }

      if (newPage > this.pageNumber) {
        newPage = this.pageNumber;
      }

      if (newPage === this.current) {
        return;
      } // 抛出一个事件


      this.$emit("pageChange", newPage);
    }

  }
};