import RightList from "./RightList.vue";
import fetchData from "@/mixins/fetchData";
import { getBlogCategories } from "@/api/blog.js";
export default {
  mixins: [fetchData([])],
  methods: {
    handleSelect(item) {
      const query = {
        page: 1,
        limit: this.limit
      };

      if (item.id === -1) {
        // 没有分类
        this.$router.push({
          name: "Blog",
          query
        });
      } else {
        // 有分类
        this.$router.push({
          name: "CategoryBlog",
          query,
          params: {
            categoryId: item.id
          }
        });
      }
    },

    async fetchData() {
      return await getBlogCategories();
    }

  },
  components: {
    RightList
  },
  computed: {
    limit() {
      return +this.$route.query.limit || 10;
    },

    categoryId() {
      return +this.$route.params.categoryId || -1;
    },

    list() {
      const totalArticleCount = this.data.reduce((a, b) => a + b.articleCount, 0);
      const result = [{
        name: "全部",
        id: -1,
        articleCount: totalArticleCount
      }, ...this.data];
      return result.map(it => ({ ...it,
        isSelect: it.id === this.categoryId,
        aside: `${it.articleCount}篇`
      }));
    }

  }
};