import Pager from "@/components/Pager";
import fetchData from "@/mixins/fetchData.js";
import { getBlogs } from "@/api/blog.js";
import { formatDate } from "@/utils";
import mainScroll from "@/mixins/mainScroll.js";
import Empty from "@/components/Empty";
export default {
  mixins: [fetchData({
    total: 0,
    rows: []
  }), mainScroll("container")],
  computed: {
    // 获取路由信息
    routeInfo() {
      const categoryId = +this.$route.params.categoryId || -1;
      const page = +this.$route.query.page || 1;
      const limit = +this.$route.query.limit || 10;
      return {
        categoryId,
        page,
        limit
      };
    }

  },
  watch: {
    // 会观察this.route是否有变化
    async $route() {
      this.isLoading = true; // 点击分页的时候页面应该在最上边

      this.$refs.container.scrollTop = 0;
      this.data = await this.fetchData();
      this.isLoading = false;
    }

  },
  components: {
    Pager,
    Empty
  },
  methods: {
    formatDate,

    async fetchData() {
      return await getBlogs(this.routeInfo.page, this.routeInfo.limit, this.routeInfo.categoryId);
    },

    handlePageChange(newPage) {
      const query = {
        page: newPage,
        limit: this.routeInfo.limit
      }; // 点击页码的时候要改变路由页码
      // 跳转到当前的分类id,当前的页容量
      // /article?page=${newPage}&limit=${this.routeInfo.limit}
      // /article/cate/${this.routeInfo.categoryId}?page=${newPage}&limit=${this.routeInfo.limit}

      if (this.routeInfo.categoryId = -1) {
        // 当前没有分类
        this.$router.push({
          name: "Blog",
          query
        });
      } else {
        // 有分类
        this.$router.push({
          name: "CategoryBlog",
          query,
          params: {
            categoryId: this.routeInfo.categoryId
          }
        });
      }
    }

  }
};